.ios-tabs {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(10%,1fr));
	margin: 5px 0;
	background-color: #eeeeee;
	padding: 3px;
	border-radius: 10px;
	cursor: pointer;
}
.ios-tabs button {
	background: none;
	border-radius: 8px;
	border: none;
	text-align: center;
	display: inline-block;
	white-space: nowrap;
	text-overflow: ellipsis;
	font-size: 12px;
	color: #000;
	padding: 8px 0;
	user-select: none;
	transition: 0.3s all;
}
.ios-tabs button.active-itab {
	background-color: #fefefe;
	box-shadow: 0 3px 5px #d2d2d2;
	font-weight: 500;
}

/* navigation + nav elements */
