/*Error color*/
.error {
	/*Don't remove*/
	background: #ffdcdc !important;
	/*Don't remove*/
}
.error-messages {
	position: absolute;
	z-index: 110;
	right: 0;
	margin: 15px;
}
.e-message {
	background: url(img/error_w.svg) 10px center / 20px no-repeat;
	background-color: #d74c39;
	border: 2px solid #d74c3950;
	padding: 13px 20px;
	padding-left: 40px;
	border-radius: 5px;
	color: #fff;
	font-size: 13px;
	margin-bottom: 13px;
	box-shadow: 0 0 5px rgba(150, 150, 150, 0.5);
	display: grid;
	grid-template-columns: 1fr 0fr;
	max-width: 400px;
	font-weight: 500;
}
.e-message .jump-error {
	padding: 0 10px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.e-message button {
	background-color: #fff;
	border: none;
	font-size: 14px;
	color: #d74c39;
	border-radius: 5px;
	font-weight: bold;
	padding: 8px 20px;
	transition: 0.3s opacity;
}
.e-message button:active {
	opacity: 0.7;
}
/* Tabs */
.file-tabs {
	border-bottom: 0.5px solid #adacac;
	background: #ccc;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(10%,1fr));
}
.file-tabs .tab:not(:last-child) {
	border-right: 0.5px solid #adacac;
}
.tab {
	padding: 5px 10px;
	color: #3d3c3d;
	transition: 0.1s all;
	cursor: pointer;
	display: inline-grid;
	grid-template-columns: 0fr 1fr;
	margin-bottom: -3px;
	overflow: hidden;
}
.tab:hover:not(.active-tab) {
	background: #a7a8a750;
}
.tab.active-tab {
	background: #e3e4e3;
	color: #1d1d1c;
}
.tab-close,
.tab-label {
	display: inline-block;
}
.tab-close {
	font-size: 20px;
	line-height: 12px;
	border-radius: 3px;
	color: #454445;
	height: 15px;
	width: 15px;
	cursor: pointer;
	user-select: none;
	padding: 3px;
	text-align: center;
	transition: 0.1s all;
}
.tab-close:active {
	opacity: 0.7;
}
.tab.active-tab .tab-close {
	opacity: 1;
}
.tab.active-tab .tab-close:hover {
	background: #ccc;
}
.tab .tab-close:hover {
	background: #aaa;
}
.tab .tab-close {
	opacity: .5;
}
.tab:hover .tab-close {
	opacity: 1;
}
.tab-label {
	font-size: 13px;
	line-height: 20px;
	padding-left: 5px;
	user-select: none;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	text-align: center;
}
/* Menus */
.savedstatus {
	display: inline-block;
	transition: 0.2s all;
	font-size: 14px;
	margin-right: 20px;
	color: #555;
}
.editor-menu {
	display: inline-block;
	color: #007aff;
	cursor: pointer;
	transition: 0.2s all;
}
.editor-menu ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	padding: 16px;
	display: inline-block;
	font-size: 15px;
}
.editor-menu > ul:first-child:hover {
	background: #007aff;
	color: #fff;
}
.editor-menu ul ul {
	margin-left: -15px;
	position: absolute;
	padding: 10px 0;
	padding-left: 1px;
	background-color: #fff;
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
	margin-top: 16px;
	box-shadow: 0 3px 10px rgba(100, 100, 100, 0.3);
	min-width: 230px;
	display: none;
	border: 0.5px solid #c8c7cc;
}
.editor-menu ul:hover ul {
	display: block;
}
.editor-menu ul ul li a {
	display: block;
	padding: 6px 15px;
	font-size: 14px;
	text-decoration: none;
	color: #1d1d1c;
	font-weight: 500;
	user-select: none;
}
.editor-menu li a:hover span {
}
.editor-menu li a span {
	float: right;
	color: #748ca5;
	font-size: 14px;
	line-height: 15px;
	text-align: right;
	padding: 3px 4px;
	border-radius: 4px;
	letter-spacing: 1px;
	margin-top: -3px;
}
.editor-menu ul ul li a:hover {
	background: #007aff;
	color: #fff;
}
.editor-menu ul ul li a:hover span {
	float: right;
	color: #ffffff;
	font-size: 14px;
	line-height: 15px;
	text-align: right;
	padding: 3px 4px;
	border-radius: 4px;
	letter-spacing: 1px;
	margin-top: -3px;
}
.menu-div {
	border-top: 1px solid #5a798b30;
	margin: 10px 15px;
}
/* Variables */
.variables-container {
	height: 40px;
	overflow: hidden;
	border-bottom: 1px solid #bbbfc4;
}
.variables {
	background: #e3e4e3;
	overflow-x: scroll;
	white-space: nowrap;
	padding: 3px 5px;
}
.code-variables, .global-variables {
	display: inline-block;
}
.global-variables {
	padding-right: 10px;
	margin-right: 10px;
	border-right: 0.5px solid #ccc;
}
.variable {
	display: inline-block;
	margin: 5px 3px;
	font-size: 11px;
	padding: 4px 12px;
	padding-bottom: 6px;
	font-weight: 500;
	border: 0.5px solid #b0bbd1;
	border-radius: 20px;
	background: linear-gradient(#d7e5f5,#cdd9ef);
	box-shadow: 0 1px 1px rgba(50,50,50,0.1);
	color: #233145;
	cursor: pointer;
	transition: 0.2s all;
	user-select: none;
}
.variable.mv {
	background: linear-gradient(#d7d6f4,#dbceef);
	color: #392c4f;
	border-color: #c4bcd2;
}
.variable:hover {
	box-shadow: 0 1px 0.5px rgba(50,50,50,.3);
}
.variable > div:before {
	content: ' ';
	display: inline-block;
  padding: 8px 7px;
  position: relative;
  margin-bottom: -4px;
	margin-top: -2px;
  margin-right: 6px;
	background: url(img/var-sheet.png) 0px / cover no-repeat;
}
.global-variables .variable {
	background: linear-gradient(to bottom, #dbf5da, #d1ebd2);
	border-color: #b2ceb7;
	color: #224425;
}
.clipboard-v > div:before {
	background-position: 0px 0px;
}
.date-v > div:before {
	background-position: -15px 0px;
}
.input-v > div:before {
	background-position: -28.7px 0px;
}
.file-v > div:before {
	background-position: -44px 0px;
}
.calc-v > div:before {
	background-position: -58px 0px;
}
.gear-v > div:before {
	background-position: -72px 0px;
}
.url-v > div:before {
	background-position: -87px 0px;
}
.gif-v > div:before {
	background-position: -101px 0px;
}
.photos-v > div:before {
	background-position: -116px 0px;
}
.mv > div:before {
	background: url(img/magic.svg) center / cover no-repeat;
	margin-right: 3px;
}
/* Find and replace */
.ace_search {
	background-color: #fff !important;
	border-radius: 15px;
	padding: 10px !important;
	padding-top: 5px !important;
	border: none !important;
	border-top: none !important;
	box-shadow: 2px 2px 50px rgba(0, 0, 0, 0.2);
}
.ace_search_field {
	border: none !important;
	padding: 5px 10px !important;
	padding-left: 35px !important;
	margin: 8px 0 !important;
	border-radius: 8px !important;
}
.ace_searchbtn, .ace_button {
	border-radius: 5px !important;
	font-family: -apple-system, BlinkMacSystemFont, Helvetica, Arial, sans-serif;
	border: 2px solid #eee !important;
	background-color: #eee !important;
	color: #007aff !important;
	margin: 0 3px !important;
	font-size: 14px !important;
	transition: 0.2s all;
}
.ace_searchbtn:hover, .ace_button:hover {
	background-color: #f5f5f5 !important;
}
.ace_searchbtn:active, .ace_button:active {
	opacity: .7;
}
.ace_button.checked {
	border: 2px solid #007aff50 !important;
	font-weight: 500;
}
.ace_searchbtn {
	padding: 1px 12px !important;
}
.ace_button {
	padding: 5px 13px !important;
}
.ace_search_options {
	margin-top: 15px;
}
.ace_search_counter {
	padding: 2px 10px !important;
	font-size: 15px;
}
[action="toggleReplace"] {
	margin-top: -5px !important;
}
.ace_searchbtn_close {
	opacity: .7;
	user-select: none;
	margin-top: 10px;
	margin-right: 10px;
	padding: 2px !important;
	background: url(img/close.svg) center / contain no-repeat !important;
}
.ace_searchbtn_close:hover {
	opacity: 1;
}
.ace_searchbtn_close:active {
	opacity: .5;
}
.ace_nomatch {
	outline: none !important;
}
.ace_nomatch .ace_search_field {
	background-color: #d74c3930 !important;
}
/* Dark Mode */
.dark .editor-navigation {
	background: #323234;
	border-color: #444;
}
.dark .editor-title {
	color: #fff;
}
.dark .result-details {
	border-color: #444;
}
.dark .result-actions {
	background-color: #78777d;
	color: #eee;
}
.dark .editor-btn a, .dark .editor-menu  {
	color: #367bf4;
}
.dark .files-header, .dark .file-pane, .dark .variables, .dark .result-text, .dark .variables-container, .dark .result-pane, .dark .preview-error,.dark .editor-error, .dark .files-error {
	background-color: #1c1c1e;
	border-color: #444;
	color: #ccc;
}
.dark .savedstatus {
	color: #eee;
}
.dark .global-variables {
	border-color: #444;
}
.dark .result-pane {
	box-shadow: 0 0 0 black;
	border-color: #444;
}
.dark .file-btn {
	background-color: #333;
}
.dark input {
	color: #fff;
}
.dark .search-input, .dark .ace_search_field {
	background: #444 url(img/search.svg) 8px center / 20px no-repeat !important;
}
.dark .large-btn {
	background-color: #f8bc3d;
	color: #000;
}
.dark .modal, .dark .download-or, .dark .editor-menu ul ul, .dark .account-settings-bar {
	background-color: #323234;
	color: #fff;
	border-color: #444;
}
.dark .download-grid > div:first-child {
	border-color: #444;
}
.dark .shortcut-filename {
	color: #fff;
}
.dark .editor-menu ul ul li a {
	color: #fff;
}
.dark .editor-menu ul ul li a span {
	color: #fff;
}
.dark .account-settings-bar {
	background-image: url(img/settings_w.svg) !important;
}
.dark .mv > div:before {
	background-image: url(img/magic_w.svg) !important;
}
.dark .variable {
	border-radius: 5px;
	background: #94c3f224 !important;
	color: #367bf4;
	font-weight: 500;
	border: none;
}
